@import url(https://fonts.googleapis.com/css2?family=Bubblegum+Sans&family=Caveat+Brush&family=Shadows+Into+Light&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
    background-color: rgb(0, 24, 50);
}

header {
    background-color:rgb(44, 44, 194);
    color:white;
    font-family: 'Bubblegum Sans', cursive;
    font-size:24px;
    padding:20px;
    text-align: center;
    position:fixed;
    width:100%;
    box-sizing: border-box;
    box-shadow:0 4px 12px rgb(0, 36, 63);
    top:0;
}

.bigInstructions {
    height:400px;
    padding-top:160px;
    background-size:100%;
    background-repeat: no-repeat;
    text-align:center;
    color:white;
    font-family: 'Bubblegum Sans', cursive;
    /* background-image:url('images/background_texture1.jpg'); */
}
.bigInstructions h1 {
    font-size:64px;
    font-weight: bold;
}
.bigInstructions h2 {
    margin-top:30px;
    font-size:30px;
}
.instructions {
 display:inline-block;

}

.logotype {
    display:inline-block;
    float:left;
 
    margin:auto;
}

.score {
    display:inline-block;
    float:right;
  
    margin:auto;
}
.wrapper {
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    text-align:center;
    box-sizing: border-box;
  }
  
  .firstContainer {
    
    max-width:900px;
    margin-top:10px;
    margin-bottom:150px;
}

.instructions-yes {
  color:rgb(3, 213, 3);
 
 }
 .instructions-no {
  color:red;
 
 }
 .instructions-not_yet {
  color:white;
 
 }
.card {
    border-radius: 2px;
    margin: 4px;
    position: relative;
    width: 20%;
    min-width:150px;
    text-align: left;
    display:inline-block;
    overflow: hidden;
    box-sizing: border-box;
    padding:12px;
    transition:all .15s;
    background:none;
    cursor: pointer;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:#3b4767;
    box-sizing: border-box;
    width:100%;
    padding:7px;
    box-shadow: 0 3px 6px rgb(5, 74, 165), 0 3px 6px rgb(12, 97, 187);
  }
  .card > .img-container > img {
    width: 100%;
    box-sizing: border-box;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .card > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
  .card > .remove:hover {
    color: #d50000;
  }
  
  .card:hover  {

    padding:5px;

  }

  .card:hover > .img-container {
    background:#455b97;
    border-radius:5px;
    box-shadow: 16px 16px 16px rgb(8, 84, 184), 0 3px 6px rgb(14, 109, 209);
  }
